import React, { Component } from "react";
import styled from "styled-components";

const HeroWrapper = styled.div`
  img {
    height: 30vh;
  }
`;

class Hero extends Component {
  render() {
    return (
      <HeroWrapper>
        <h2>hero</h2>
      </HeroWrapper>
    );
  }
}

export default Hero;
